    buyTicket({state,commit/*,dispatch*/},numbers) {
      // if(state.approvedLottery) {
        let code = ""
        for(let i = 0;i<6;i++) {
          code += (numbers[i]<10?'0':'')+numbers[i]
        }
        state.contracts.tokenLottery.methods.buyTickets(
          state.lottery.currentLotteryId,
          [code]
        ).send({
          from: state.account.address
        }).then(()=>{
          commit('show_success','Success! Purchased ticket for 100 $BQB')
          commit('read_lottery')
          commit('read_liquidity')
        })
      // } else {
      //   state.contracts.tokenBQB.methods.approve(
      //     ADDR_LOTTERY,
      //     UINT256_MAX
      //   ).send({
      //     from: state.account.address
      //   }).then(()=>{
      //     state.approvedLottery = true
      //     dispatch('buyTicket',numbers,callback)
      //   })
      // }
    },
    harvest({state,commit},pid) {
      state.contracts.tokenMaster.methods.canHarvest(
        pid, state.account.address
      ).call().then(ret=>{
        if(ret) {
          state.contracts.tokenMaster.methods.deposit(
            pid,
            0,
            ADDR_NULL
          ).send({
            from: state.account.address
          }).then(()=>{
              commit("read_liquidity")
          })
        } else {
          commit('show_warning','Cannot harvest! Still locked.')
        }
      })      
      if(pid == 1)    // BQB Staking
        state.contracts.tokenMaster.methods.canHarvestFTM(
          pid, state.account.address
        ).call().then(ret=>{
          if(ret) {
            state.contracts.tokenMaster.methods.harvestFTM(pid,state.account.address).send({
              from: state.account.address
            }).then(()=>{
              commit('read_liquidity')
            })
          } else {
            commit('show_warning','Cannot harvest FTM! Still locked.')
          }
        })
    },
    claim({state,commit}) {
      if(state.lottery.myWinning==null || !BigNumber(state.lottery.myWinning.pendingRewards).isGreaterThan(0)) {
        commit('show_warning','No winnigs')
        return;
      }
      if(state.lottery.myResultIds==null || state.lottery.myResultIds.length==0) {
        return;
      }
      let id = state.lottery.myResultIds[state.lottery.myResultIds.length-state.lottery.myResultIndex-1]
      state.contracts.tokenLottery.methods.claimTickets(
        id,
        state.lottery.myWinning.new_ticketIds,
        state.lottery.myWinning.new_brackets
      ).send({
        from: state.account.address
      }).then(()=>{
        commit('read_liquidity')
        commit('read_lottery')
      })
    },
    getAmountSwap({state,commit},params) {
      let path = []
      if(params.coinIn=='FTM')
        path.push(ADDR_TOKEN_FTM)
      else if(params.coinIn=='BQB')
        path.push(ADDR_TOKEN_BQB)
      if(params.coinOut=='FTM')
        path.push(ADDR_TOKEN_FTM)
      else if(params.coinOut=='BQB')
        path.push(ADDR_TOKEN_BQB)
      return new Promise((resolve,reject)=>{
        if(path.length!=2)
          reject('parameter error')
        if(params.baseCoin==1) {
          state.contracts.tokenRouter.methods.getAmountsOut(params.amount,path).call().then(ret=>{
            resolve(ret[1])
            commit('read_liquidity')
          })
        } else {
          state.contracts.tokenRouter.methods.getAmountsIn(params.amount,path).call().then(ret=>{
            resolve(ret[0])
            commit('read_liquidity')
          })
        }
      })
    },
    contractLink() {
      const url = 'https://ftmscan.com/address/' + ADDR_LIQUIDITY_GENERATOR
      window.open(url, '_blank')
    },
    claimBQB({state,commit}) {
      state.contracts.tokenDistributor.methods.calculateCredit(
        state.account.address
      ).call().then(ret=>{
        if(ret > 0) {
          state.contracts.tokenDistributor.methods.claim().send({
            from: state.account.address
          }).then(()=>{
            commit('show_success', 'Success!')
          })
        } else {
          commit('show_warning','Cannot Claim! Current Balance is Zero.')
        }
      })
    },
    claimBQBForBonus({state,commit}) {
      state.contracts.tokenBonusDistributor.methods.calculateCredit(
        state.account.address
      ).call().then(ret=>{
        if(ret > 0) {
          state.contracts.tokenBonusDistributor.methods.claim().send({
            from: state.account.address
          }).then(()=>{
            commit('show_success', 'Success!')
          })
        } else {
          commit('show_warning','Cannot Claim! Current Balance is Zero.')
        }
      })
    },
    depositFTM({state,commit}, params) {
      state.contracts.tokenLiquidityGenerator.methods.deposit(
      ).send({
        from: state.account.address,
        value:BigNumber(params.amount).shiftedBy(18).integerValue().toString()
      }).then(()=>{
        commit('show_success', 'Success!');
        commit('read_liquiditygenerator');
      })
    }
